import string from './css.js'

const demo = document.querySelector('#demo')
const demo2 = document.querySelector('#demo2')
let n = 1
demo.innerHTML = string.substring(0, n)//è¿”å›žå­—ç¬¦ä¸²çš„å­å­—ç¬¦ä¸²å†™å…¥åˆ°é¡µé¢ä¸­
demo2.innerText = string.substring(0, n)//å†™åˆ°é¡µé¢çš„æ–‡æœ¬ä¸­

let time = 50
const run = () => {//è¿è¡Œ
  if (id > string.length) {//å¦‚æžœidå¤§äºŽå­—ç¬¦ä¸²çš„é•¿åº¦
    window.clearInterval(id)//æ¸…é™¤è°ƒç”¨å‡½æ•°
    return//ç›´æŽ¥è¿”å›ž
  }
  n += 1
  demo.innerHTML = string.substring(0, n)
  demo2.innerText = string.substring(0, n)
  demo2.scrollTop = demo2.scrollHeight//demo2çš„æ»šåŠ¨æœ€å¤§é«˜åº¦ç­‰äºŽè‡ªèº«çš„æ»šåŠ¨é«˜åº¦
}
const play = () => {//è°ƒç”¨äº‹ä»¶
  return setInterval(run, time)//æŒ‰æ—¶é—´è°ƒç”¨è¿™ä¸ªå‡½æ•°éƒ½ä¼šæœ‰ä¸ªidå€¼ï¼Œè¡¨ç¤ºè°ƒç”¨äº†å¤šå°‘æ¬¡
}
let id = play()
const pause = () => {//æ¸…é™¤è°ƒç”¨äº‹ä»¶
  return window.clearInterval(id)
}
const play1 = () => {
  pause()
  id = play()
}
const slow = () => {
  pause()
  time = 100
  id = play()
}
const normal = () => {
  pause()
  time = 25
  id = play()
}
const fast = () => {
  pause()
  time = 0
  id = play()
}
document.querySelector('#btnPause').onclick = pause
document.querySelector('#btnPlay').onclick = play1
document.querySelector('#btnSlow').onclick = slow
document.querySelector('#btnNormal').onclick = normal
document.querySelector('#btnFast').onclick = fast